                      </el-form-item>
                      <span v-else></span>
                      <el-button type="success" icon="el-icon-refresh" @click="changeNO" style="padding: 5px; margin:0;"
                        circle></el-button>
                    </el-form>
                  </div>
                </div>
                <!--  -->
                <div style="background-color:bisque">
                  <div style="text-align: left;display: flex;justify-content: space-between;">
                     <el-button type="primary" size="mini" @click="serialNumberAdd()">
                    </el-button>
                  </div>
                  <div v-for='(serialNumber,index) in marketPlanSerialNumber'>
                    <el-row :gutter="5" class="e-rows">
                      <el-col :span="7">
                        <el-form-item label=":">
                          <el-input prefix-icon="el-icon-edit" v-model="serialNumber.startNo" size="mini"
                            placeholder="..."></el-input>
                        </el-form-item>
                      </el-col>
                      <el-col :span="1"><span>--</span> </el-col>
                      <el-col :span="4">
                        <el-input prefix-icon="el-icon-edit" v-model="serialNumber.endNo" size="mini" placeholder="">
                        </el-input>
                      </el-col>
                      <el-col :span="5">
                        <el-input prefix-icon="el-icon-edit" v-model="serialNumber.note" size="mini" type='text'
                          placeholder=" "></el-input>
                      </el-col>
                      <el-col :span="3">
                        <el-tooltip effect="light" content="" placement="right">
                          <el-button type="danger" icon="el-icon-delete" style="padding: 5px; margin:0;"
                            @click="deletSerialNo(index)" circle></el-button>
                        </el-tooltip>
                      </el-col>
                    </el-row>
                  </div>
                </div>
              </div>
              <div style="text-align:right;">
                <el-button type="primary" size="mini" @click="addBox"></el-button>
              </div>
              <el-table :data="marketPlanDetails" border height="200" style="width: 100%;">
                <el-table-column prop="sid" align="left" label="" width="100">
                </el-table-column>
                <el-table-column prop="clientName" align="left" label="" width="100">
                </el-table-column>
                <el-table-column prop="abbrName" align="left" label="" width="100">
                </el-table-column>
                <el-table-column prop="name" align="left" label="" width="100">
                </el-table-column>
                <el-table-column align="left" label="" width="80">
                  <template slot-scope="scope">{{ scope.row.quantity}}</template>
                </el-table-column>
                <el-table-column prop="specs" align="left" label="" width="120">
                </el-table-column>
                <el-table-column prop="proNo" align="left" label="" width="120">
                </el-table-column>
                <el-table-column prop="name" align="left" label="" width="100">
                  <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                      <div v-for="(serial,index) in  scope.row.marketPlanSerialNumber">
                        <p>: {{ serial.startNo}}- {{ serial.endNo}} ;: {{ serial.note}} </p>
                      </div>
  
                      <div slot="reference" class="name-wrapper">
                        <el-tag size="medium"></el-tag>
                      </div>
                    </el-popover>
                  </template>
                </el-table-column>
  
                <el-table-column prop="notes" align="left" label="" width="120">
                </el-table-column>
                <el-table-column align="left" label="" width="195">
                  <template slot-scope="scope">
                    <el-button @click="editeDetail(scope.$index,scope.row)" style="padding: 3px 4px 3px 4px;margin: 2px"
                      size="mini">
                    </el-button>
                    <el-button type="danger" style="padding: 3px 4px 3px 4px;margin: 2px" size="mini"
                      @click="deleteDetail(scope.$index,scope.row)">
                    </el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </el-form>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible=false"> </el-button>
          <el-button type="primary" @click="add('addEmpForm')"> </el-button>
        </div>
      </el-dialog>
  
      <el-dialog v-dialogDrag :title="dialogMaterialTitle" style="padding: 0px;" :close-on-click-modal="false"
        :visible.sync="dialogMaterialVisible" @close="cancelchoose" width="480px">
        <div style="width: 100% " v-if="dialogMaterialVisible">
          <div style="padding: 0px;display:flex;justify-content:space-between;align-items: center">
            <!--    -->
            <div style="display: inline">
              <el-input placeholder=",..." clearable @change="keywordsMaterialChange"
                style="width: 300px;margin: 0px;padding: 0px;" size="mini" @keyup.enter.native="searchMaterialData"
                prefix-icon="el-icon-search" v-model="keyMaterialwords">
              </el-input>
              <el-button type="primary" size="mini" style="margin-left: 5px" icon="el-icon-search"
                @click="searchMaterialData">
              </el-button>
            </div>
          </div>
          <!--  -->
          <el-table :data="materialList" border style="width: 100%" @row-click="showRow">
            <el-table-column align="center" fixed label="" width="80">
              <template slot-scope="scope">
                <el-radio :label="scope.$index" v-model='selectedM'>&nbsp;</el-radio>
              </template>
            </el-table-column>
            <el-table-column prop="producteName" align="left" fixed label="" width="120">
            </el-table-column>
            <el-table-column prop="specs" align="left" fixed label="" width="120">
            </el-table-column>
            <el-table-column align="left" label="" width="80">
                <template slot-scope="scope">{{ scope.row.quantity}}</template>
            </el-table-column>
            <el-table-column prop="proNo" align="left" fixed label="" width="120">
            </el-table-column>
            <el-table-column prop="notes" align="left" fixed label="" width="120">
            </el-table-column>
          </el-table>
          <div style="display: flex;justify-content: flex-end;margin: 2px">
            <el-pagination background :page-size="10" :pager-count="11" :current-page="currentMaterialPage"
              @current-change="currentMaterialChange" layout="prev, pager, next" :total="totalMaterialCount">
            </el-pagination>
          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogMaterialVisible=false"> </el-button>
          <el-button type="primary" @click="chooseMaterial()"> </el-button>
        </div>
      </el-dialog>
      <el-table :data="plans" border style="width: 100%">
        <el-table-column prop="marketPlanNo" align="left" label="" width="150">
        </el-table-column>
        <el-table-column width="100" align="left" label="">
          <template slot-scope="scope">{{ scope.row.createDate | formatDate}}</template>
        </el-table-column>
        <el-table-column prop="status" width="80" align="left" :formatter="formatStatus" label="">
        </el-table-column>
        <el-table-column align="left" label="" width="195">
          <template slot-scope="scope">
            <el-button @click="showEditEmpView(scope.$index,scope.row)" style="padding: 3px 4px 3px 4px;margin: 2px"
              size="mini">
            </el-button>
            <el-button type="danger" style="padding: 3px 4px 3px 4px;margin: 2px" size="mini"
              @click="deleteData(scope.row)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="display: flex;justify-content: flex-end;margin: 2px">
        <el-pagination background :page-size="10" :pager-count="11" :current-page="currentPage"
          @current-change="currentChange" layout="prev, pager, next" :total="totalCount">
        </el-pagination>
      </div>
    </div>
  
  </template>
  <script>
    import {
      Message
    } from 'element-ui'
    export default {
      data() {
        return {
          lastPlanDetail: {
            startNo: 'J00000001',
            endNo: "J00000100"
          },
          lastPlanmarketPlanDetails: [{
            startNo: 'J00000001',
            endNo: "J00000100"
          }, {
            startNo: 'K00000001',
            endNo: "K00000100"
          }],
          // 
          pickerOptions2: {
            shortcuts: [{
              text: '',
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit('pick', [start, end]);
              }
            }, {
              text: '',
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                picker.$emit('pick', [start, end]);
              }
            }, {
              text: '',
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                picker.$emit('pick', [start, end]);
              }
            }]
          },
          value4: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
          value5: '',
          //***********************************________*******************************
          keywords: '',
          tableLoading: false,
          advanceSearchViewVisible: false,
          rules: {
            // note: [{required: true, message: ':', trigger: 'blur'}],
            // name: [{required: true, message: ':', trigger: 'blur'}],
            // quantity: [{required: true, message: ':', trigger: 'blur'}],
            // unitId: [{required: true, message: ':', trigger: 'blur'}],
          },
          types: 2,
          midProduct: [],
          plans: [],
          clients: [],
          units: [],
          dialogTitle: '',
          currentPage: 1,
          totalCount: -1,
          // 
          totalMaterialCount: -1,
          keyMaterialwords: '',
          currentMaterialPage: 1,
          plan: {
            materialType: '',
            notes: '',
            serialNumber: '',
            clientName: ''
          },
          detail: {
            sid: 0,
            id: 0,
            clientId: 0,
            clientName: '',
            abbrName: '',
            quantity: '',
            notes: '',
            name: '',
            proId: '', //Id
            specs: "", //
            stamp: '', //
            proNo: '', //
            marketPlanSerialNumber: [],
            type: '',
            specifications: ''
          },
          marketPlanSerialNumber: [],
          serialNumber: {
            sid: 1,
            id:0,
            startNo: '',
            endNo: '',
            note: '',
            clientId:0,
            productId:0,
            type: "new"
          },
          marketPlanDetails: [],
          dialogFormVisible: false,
          currentRowData: {}, //
          currentIndex: '', //
          depTextColor: '',
          showOrHidePop: false,
          showHidePop: false,
          defaultProps: {
            label: 'name',
            isLeaf: 'leaf',
            children: 'child'
          },
          maxSid: 0,
          currentDeleteData: [],
          materials: [],
          materialList: [],
          products: [],
          dialogMaterialVisible: false,
          dialogMaterialTitle: '',
          selectMaterial: {}, //
          selectedM: {},
          filterText: '',
          lastPlanDetail: '', //;
          crrentSelectShop: {}, //
          isCurrentShop: true,
          isprarent: false,
          sugestParentstartNo: '', //..
          sugestParentEndNo: '', //
          sugestSelfEndNumbers: '',
          sugestSelfNumbers: '', //
          currentParentShop: {},
          currentShop: {},
          lastPlanDetail: {
            startNo: '',
            endNo: '',
          },
          serialNuberDeleted: [], //
          serialNuberUpdate: [], //
          serialNuberList: [], //
  
        }
      },
      mounted: function () {
        this.initData();
      },
      // watch: {
      //   filterText(val) {
      //     this.$refs.tree2.filter(val);
      //   }
      // },
      methods: {
        // ***********
        serialNumberAdd() {
          var sid = 0;
          if (this.marketPlanSerialNumber.length) {
            var maxId = 0;
            //Id+1
            this.marketPlanSerialNumber.forEach(function (v, i, arr) {
              maxId = v.sid > maxId ? v.sid : maxId;
            })
            sid = maxId + 1;
          } else {
            sid = 1;
          }
          this.marketPlanSerialNumber.push({
            sid: sid,
            id:0,
            startNo: '',
            endNo: '',
            clientId:0,
            productId:0,
            type: "new"
          });
        },
        deletSerialNo(index) {
          var deletData = this.marketPlanSerialNumber.splice(index, 1);
          var conditions = deletData[0].endNo == '' ||
            deletData[0].startNo == '' ||
            deletData[0].type == 'new';
          if (!conditions) {
            deletData[0].type = 'delete';
            this.serialNuberDeleted.push(deletData[0])
          }
          console.log(deletData);
        },
        // ***********
        filterNode(value, data) {
          if (!value) return true;
          return data.label.indexOf(value) !== -1;
        },
        showRow(row) {
          this.selectedM = this.materialList.indexOf(row);
          this.selectMaterial = row;
          console.log(this.selectMaterial);
        },
        showMaterial() {
          this.loadMaterial();
  
        },
        chooseMaterial() {
          console.log('')
          // this.selectMaterial
          let vm = this;
  
          if (this.selectMaterial.id > 0) {
            const msg = this.selectMaterial.producteName + "" + this.selectMaterial.specification + ";"
            this.$confirm('' + msg + "", '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              vm.setDetailData(vm);
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              });
            });
          } else {
            this.$message({
              type: 'info',
              message: ''
            })
          }
  
        },
        setDetailData(that) {
          var v = that.selectMaterial;
          console.log(v);
  
          that.detail.name = v.producteName;
          that.detail.proId = v.id; //Id
          that.detail.specs = v.specification;
          that.detail.proNo = v.proType
          that.getCurrentPlans();
          this.dialogMaterialVisible = false;
        },
        cancelchoose: function () {
          console.log('')
          this.selectMaterial = {};
          this.selectedM = {};
        },
        formatStatus: function (row, column) {
          return row.materialType == 1 ? '' : '';
        },
        //materials: [["", 1, 38]]
        selectType(e, v, icon) {
          let that = this;
          console.log('changne'); //products  materials
          this.types = e;
          if (e == 2) {
            this.midProduct = [];
            this.midProduct = this.materials.concat(this.midProduct);
            this.emptyDetail()
          } else {
            this.midProduct = [];
            this.midProduct = this.products.concat(this.midProduct);
            this.emptyDetail()
          }
        },
        matterSelect(e) {
          console.log(e);
          let that = this;
          this.midProduct.forEach(function (v, i, arr) {
            if (v.id == e) {
              that.detail.unitId = v.unitId;
              that.detail.unitName = v.unitName;
              that.detail.name = v.name;
              that.detail.materialId = v.id; //Id
              that.detail.specifications = v.specs;
  
            }
          })
        },
        addBox() {
          let that = this;
          console.log("asdfadsfasdfasd");
          if (this.detail.quantity == '' || this.detail.quantity == undefined) {
            Message('');
            return false;
          }
          if (this.detail.name == '' || this.detail.name == undefined) {
            Message('');
            return false;
          }
          if (this.detail.sid == "") {
            if (this.marketPlanDetails.length > 0) {
              //this.types /
              //
              var flag = false;
              this.marketPlanDetails.forEach(function (v, i, arr) {
                if (v.materialId == that.detail.materialId && v.mtype == that.types) {
                  flag = true;
                  v.quantity = parseFloat(that.detail.quantity) + parseFloat(v.quantity);
                }
              });
              if (!flag) {
                this.maxSid += 1;
                this.detail.sid = this.maxSid;
                this.detail.type = 'new'
                // this.detail.mtype = this.types;
                this.marketPlanDetails.push(this.detail);
              }
  
            } else {
              this.detail.sid = 1;
              this.detail.type = 'new'
              this.detail.mtype = this.types;
              this.maxSid += 1;
              this.marketPlanDetails.push(this.detail);
            };
          } else {
            if (this.marketPlanDetails.length) {
              for (let index = 0, n = this.marketPlanDetails.length; index < n; index++) {
                var e = this.marketPlanDetails[index];
                if (e.type === 'odd') {
                  this.detail.type = 'update'
                  if (e.sid == this.detail.sid) {
                    this.marketPlanDetails.splice(index, 1);
                    this.marketPlanDetails.push(this.detail);
                  }
                } else {
                  if (e.sid == this.detail.sid) {
                    this.marketPlanDetails.splice(index, 1);
                    this.marketPlanDetails.push(this.detail);
                  }
                }
  
              }
            }
          }
          this.detail.marketPlanSerialNumber = that.marketPlanSerialNumber;
  
          this.clearSerialNumbers();
          // console.log( this.marketPlanDetails);
          this.emptyDetail();
        },
        //
        clearSerialNumbers() {
          let that = this;
          that.marketPlanSerialNumber = [];
          that.marketPlanSerialNumber.push({
            sid: 1,
            id:0,
            startNo: '',
            endNo: '',
            type: 'new',
            note: ''
          });
        },
  
        editeDetail(index, row) {
          this.detail = row;
        },
        deleteDetail(index, row) {
          console.log('delete');
          var deletes = this.marketPlanDetails.splice(index, 1);
          if (deletes[0].type === "odd") {
            deletes[0].type = 'delete'
            this.currentDeleteData.push(deletes[0]);
          }
          this.maxSid -= 1;
        },
        emptyDetail() {
          this.detail = {
            sid: 0,
            id: 0,
            name: '',
            quantity: '',
            unitId: '',
            notes: '',
            unitName: '',
            specifications: '',
            type: '',
          };
        },
        initData() {
          var _this = this;
          _this.loadData();
          this.getRequest("/productionplan/basedata").then(resp => {
            if (resp && resp.status == 200) {
              var data = resp.data;
              // _this.units = data.root.units;
              // _this.clients = data.root.clients;
              var ma = data.root.clients;
              var mater = [];
              ma.forEach(function (v, i, arr) {
                var name = v[0],
                  id = v[1],
                  parentClientId = v[2];
                mater.push({
                  name: name,
                  id: id,
                  parentClientId: parentClientId,
                  parentName: v[3],
                  address: v[4],
                  phone: v[5],
                  abbreviation: v[6],
  
                })
              });
              _this.clients = mater;
              _this.midProduct = [];
              _this.midProduct = _this.materials.concat(_this.midProduct);
              var pro = [];
              var p = data.root.products;
              p.forEach(function (v, i, arr) {
                pro.push({
                  id: v[0],
                  producteName: v[1],
                  specification: v[2],
                  proType: v[3],
                  mark: v[4],
                  color: v[5],
                })
              });
              _this.products = pro;
            }
          })
  
        },
        keywordsChange(val) {
          if (val == '') {
            this.loadData();
          }
        },
        keywordsMaterialChange(val) {
          if (val == '') {
            this.loadMaterial();
          }
        },
        handleNodeClick(data) {
          let that = this;
          that.tableLoading = true;
          this.crrentSelectShop = data;
          this.detail.clientId = data.id;
          this.detail.clientName = data.name;
          this.detail.abbrName = data.abbreviation;
          this.showOrHidePop = false;
          this.depTextColor = '#606266';
          this.getCurrentPlans()
        },
        showCurrentNo() {
  
          if (this.isprarent) {
            this.isprarent = false;
          } else {
            this.isprarent = true;
          }
        },
        // ;
        changeNO() {
          let that = this;
          if (that.isCurrentShop) {
            that.lastPlanDetail = that.currentShop;
            that.isCurrentShop = false;
          } else {
            that.lastPlanDetail = that.currentParentShop;
            that.isCurrentShop = true;
          }
        },
        getCurrentPlans() {
          let that = this;
          if (this.crrentSelectShop.id == undefined) {
            this.$message({
              type: 'info',
              message: ''
            })
          } else if (this.detail.proId == undefined || this.detail.proId == '') {
            this.$message({
              type: 'info',
              message: ''
            })
          } else {
            this.getRequest("/productionplan/getPlanDetails?clientId=" + this.crrentSelectShop.id + "&productId=" + this
              .detail.proId).then(resp => {
              that.tableLoading = false;
              if (resp && resp.status == 200) {
                var data = resp.data;
                that.currentParentShop = data.root.parent;
                if (that.currentParentShop.length) {
                  var n = that.currentParentShop.length - 1;
                  that.currentParentShop = that.currentParentShop[n]
                  var lastMaxNumber = that.currentParentShop[n].endNo;
                  that.sugestParentstartNo = that.sugestStartNo(lastMaxNumber, 1);
                } else { //000001;
                  that.sugestParentstartNo = '00000001';
                  that.currentParentShop = {};
                }
                if (that.detail.quantity != '') {
                  that.sugestParentEndNo = that.sugestStartNo(that.currentParentShop[n].endNo, parseInt(that.detail
                    .quantity) + 1)
                }
                that.setStartSerialNo(that.sugestParentstartNo, that.sugestParentEndNo.endNo, "parent");
                that.lastPlanDetail = that.currentParentShop;
                that.currentShop = data.root.self;
  
                if (that.currentShop.length) {
  
                  var m = that.currentShop.length - 1;
                  that.currentShop = that.currentShop[m];
                  var lastMisNumber = that.currentShop[m].endNo;
                  that.sugestSelfNumbers = that.sugestStartNo(lastMisNumber, 1);
                } else { //000001;
                  that.sugestSelfNumbers = "00000001"
                  that.currentShop = {};
                }
              }
            })
          }
        },
        setEndNumber: function (e) {
          console.log(e) + "********************8";
          // e.target.value
          let that = this;
          if (that.isCurrentShop) {
            that.lastPlanDetail = that.currentShop;
            if (that.currentShop.length) {
  
              var m = that.currentShop.length - 1;
              that.sugestSelfEndNumbers = that.sugestStartNo(that.currentShop[m].endNo, parseInt(e) + 1);
              this.marketPlanSerialNumber[0].startNo = that.sugestSelfNumbers;
              this.marketPlanSerialNumber[0].endNo = that.sugestSelfEndNumbers;
              this.marketPlanSerialNumber[0].note = '';
              this.marketPlanSerialNumber[0].clientId = this.crrentSelectShop.id;
              this.marketPlanSerialNumber[0].productId =this.detail.proId;
  
  
             
            } else {
              var baseData = that.sugestSelfNumbers
              that.setCurrentEndData(baseData, e);
            }
          } else {
            that.lastPlanDetail = that.currentParentShop;
            if (that.currentParentShop.length) {
              var n = that.currentParentShop.length - 1;
              that.sugestParentEndNo = that.sugestStartNo(that.currentParentShop[n].endNo, parseInt(e) + 1);
              this.marketPlanSerialNumber[0].startNo = that.sugestParentstartNo;
              this.marketPlanSerialNumber[0].endNo = that.sugestParentEndNo;
              this.marketPlanSerialNumber[0].note = '';
              this.marketPlanSerialNumber[0].clientId = this.crrentSelectShop.id;
              this.marketPlanSerialNumber[0].productId =this.detail.proId;
            } else {
              var baseData = that.currentParentShop
              that.setCurrentEndData(baseData, e);
            }
          }
        },
        setCurrentEndData(baseData, e) {
          var baseLen = baseData.length;
          var currentData = parseInt(baseData) + parseInt(e);
          var currentDataLen = currentData.toString().length;
          var zerolen = baseLen - currentDataLen;
          if (zerolen) {
            for (let i = 0; i < zerolen; i++) {
              currentData = "0" + currentData;
            }
          }
  
          this.marketPlanSerialNumber[0].endNo = currentData;
        },
  
        //:
        sugestStartNo(str, nu) {
          let that = this;
          // var str = "ox002VtII0000UUY";
          var getNum = function (Str, isFilter) {
            isFilter = isFilter || false;
            if (typeof Str === "string") {
              // var arr = Str.match(/(0\d{2,})|([1-9]\d+)/g);
              //"/[1-9]\d{1,}/g",19,().
              //"/\d{2,}/g",  
              var arr = Str.match(isFilter ? /[0-9]\d{1,}/g : /\d{1,}/g);
              console.log(arr);
              return arr.map(function (item) {
                //
                //00000---->0
                //001515
                // return parseInt(item);
                //0
                return item;
              });
            } else {
              return [];
            }
          };
          var startIndex = str.indexOf(getNum(str)[0]);
          var startStr = str.substr(0, startIndex);
          var endStr = str.substr(startIndex + getNum(str)[0].length, str.length - 1);
          //;
          var noLength = getNum(str)[0].length;
          var currentNumber = parseInt(getNum(str)[0]) + nu;
          var cNo = currentNumber.toString().length;
          var subNo = (currentNumber - cNo);
          if (subNo) {
            // 
            for (var i = 0; i < subNo; i++) {
              currentNumber += "0";
            }
          }
          return startStr + currentNumber + endStr;
  
        },
        // :type ;self  parent
        setStartSerialNo(sugestNo, endNo, type) {
          if (this.marketPlanSerialNumber.length) {
            this.marketPlanSerialNumber[0].startNo = sugestNo;
            this.marketPlanSerialNumber[0].endNo = endNo;
            this.marketPlanSerialNumber[0].note = '';
          }
  
        },
  
        clearChoose() {
          this.detail.clientId = 0;
          this.detail.clientName = "";
        },
        showDepTree() {
          this.showHidePop = !this.showHidePop;
        },
        selectChange(e) {
          let that = this;
          console.log(e);
          if (that.units) {
            that.units.forEach(function (v, arr) {
              if (v.id == e) {
                console.log(v.id);
                that.detail.unitName = v.name;
  
              }
            })
          }
  
        },
        currentChange(currentChange) {
          this.currentPage = currentChange;
          console.log(this.currentPage);
          this.loadData();
        },
        currentMaterialChange(currentChange) {
          this.currentMaterialPage = currentChange;
          this.loadMaterial();
        },
        addAndFlushData() {
          this.dialogTitle = "";
          let that = this;
          // this.getRequest("/productionplan/getSerialNumber").then(resp => {
          //   that.tableLoading = false;
          //   if (resp && resp.status == 200) {
          //     var data = resp.data;
          //     that.plan.serialNumber = data.root.serialNumber;
          //   }
          // })
          that.marketPlanSerialNumber = [];
  
          that.marketPlanSerialNumber.push(this.serialNumber);
  
          that.dialogFormVisible = true;
  
        },
        searchData() {
          this.loadData();
        },
        searchMaterialData() {
          this.loadMaterial();
        },
        showEditEmpView(index, row) {
          console.log(row)
          var that = this;
          that.dialogTitle = "";
          that.plan = row;
          that.plan.createDate = row.createDate;
          var currentPlan = _.cloneDeep(row);
          if (currentPlan.detail != undefined) {
            currentPlan.detail.forEach(function (v, i, arr) {
              v.type = 'odd';
              v.sid = i + 1;
            })
            this.marketPlanDetails = currentPlan.detail;
            this.maxSid = currentPlan.detail.length;
          }
          that.dialogFormVisible = true;
        },
        add(addEmpForm) {
          this.dialogFormVisible = true;
          var _this = this;
          var plans = _.cloneDeep(this.plan);
          if (this.marketPlanDetails.length == 0) {
            this.$message({
              type: 'info',
              message: ''
            });
            return false;
          }
          var cache = [];
          var currentDetail = this.marketPlanDetails.concat(this.currentDeleteData);
          var str = JSON.stringify(currentDetail, function (key, value) {
            if (typeof value === 'object' && value !== null) {
              if (cache.indexOf(value) !== -1) {
                // Circular reference found, discard key
                return;
              }
              // Store value in our collection
              cache.push(value);
            }
            return value;
          });
          cache = null;
          plans.marketPlanDetails = str;
          this.$refs[addEmpForm].validate((valid) => {
            if (valid) {
              // this.plan.buyedDate=new Date(this.plan.buyedDate);
              var flag = this.validateplanNo();
              if (this.plan.id) {
                //
                this.tableLoading = true;
                console.log(this.plan);
  
                this.postRequest("/productionplan/update", plans).then(resp => {
                  _this.tableLoading = false;
                  if (resp && resp.status == 200) {
                    // var data = resp.data;
                    _this.dialogFormVisible = false;
                    _this.loadData();
                    _this.emptyData();
                    _this.emptyDetail();
                    _this.clearSerialNumbers();
  
                  }
                })
              } else {
                //
                this.tableLoading = true;
                if (this.plan.clientId == 0 || this.plan.clientId == '') {
                  this.plan.clientId = 0;
                  plans.materialType = 1;
                } else {
                  plans.materialType = 2;
                }
                console.log(plans);
                this.postRequest("/productionplan/add", plans).then(resp => {
                  _this.tableLoading = false;
                  if (resp && resp.status == 200) {
                    var data = resp.data;
                    _this.dialogFormVisible = false;
                    _this.loadData();
                    _this.emptyData();
                    _this.emptyDetail();
                    _this.clearSerialNumbers();
                  }
                })
              }
            } else {
              return false;
            }
          });
        },
        validateplanNo() {
          var flag = true;
  
          return flag;
        },
        cancelOpt() {
          this.dialogFormVisible = false;
          this.loadData();
          // var index=this.currentIndex;
          // this.plans[index] =this.currentRowData;
          console.log('o(**)o')
  
        },
        deleteData(row) {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.doDelete(row.id);
          }).catch(() => {});
        },
        doDelete(ids) {
          this.tableLoading = true;
          var _this = this;
          this.getRequest("/productionplan/deletmark?id=" + ids).then(resp => {
            _this.tableLoading = false;
            if (resp && resp.status == 200) {
              var data = resp.data;
              _this.loadData();
            }
          })
        },
        loadData() {
          var _this = this;
          this.tableLoading = true;
          this.getRequest("/productionplan/findbypage?page=" + this.currentPage + "&size=10&queryName=" + this.keywords)
            .then(
              resp => {
                this.tableLoading = false;
                if (resp && resp.status == 200) {
                  var data = resp.data.data;
                  _this.plans = data;
                  _this.totalCount = resp.data.total;
                  _this.emptyData();
                }
              })
        },
        loadMaterial() {
          var _this = this;
          this.tableLoading = true;
          this.getRequest("/sales/findbypage?page=" + this.currentMaterialPage + "&size=10&queryName=" + this
            .keyMaterialwords).then(resp => {
            this.tableLoading = false;
            if (resp && resp.status == 200) {
              var data = resp.data.data;
              _this.materialList = data;
              _this.totalMaterialCount = resp.data.total;
              _this.dialogMaterialVisible = true;
              // _this.emptyData();
              // 
            }
          })
        },
  
        emptyData() {
          this.marketPlanDetails = [];
          this.plan = {
            materialType: '',
            notes: '',
            serialNumber: '',
            clientId: 0,
            clientName: ''
          }
  
        }
      },
      computed: {
        routes() {
          return this.$store.state.routes
        }
      }
    }
  
  </script>
  <style>
    .el-submenu {
      width: 180px;
      min-width: 175px;
    }
  
    .demo-table-expand {
      font-size: 0;
    }
  
    .demo-table-expand label {
      width: 50px;
      color: #2974df;
    }
  
    .demo-table-expand .el-form-item {
      margin-left: 10px;
      margin-bottom: 0;
      width: auto;
    }
  
  </style>
  


